import React, { useState, useEffect, useMemo } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import {
  BookOpen,
  Clock,
  Users,
  Star,
  Play,
  Search,
  Filter,
  Plus,
  Edit,
  Trash2,
  Eye,
  Heart,
  TrendingUp,
  Award,
  ChevronDown,
  Grid,
  List,
  SortAsc,
  SortDesc,
  X,
  Sparkles,
  Target,
  BookMarked,
  Timer,
  CheckCircle
} from 'lucide-react';
import { courseAPI, handleApiError } from '../services/api';
import toast from 'react-hot-toast';

const CourseView = ({ user }) => {
  const [courses, setCourses] = useState([]);
  const [enrollments, setEnrollments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [enrolling, setEnrolling] = useState(new Set());
  const [searchParams, setSearchParams] = useSearchParams();
  const [viewMode, setViewMode] = useState('grid');
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showFilters, setShowFilters] = useState(false);
  const navigate = useNavigate();

  // Filter and search states
  const [filters, setFilters] = useState({
    search: searchParams.get('search') || '',
    difficulty: searchParams.get('difficulty') || 'all',
    category: searchParams.get('category') || 'all',
    rating: searchParams.get('rating') || 'all',
    duration: searchParams.get('duration') || 'all',
    sort: searchParams.get('sort') || 'newest'
  });

  useEffect(() => {
    fetchCourses();
    if (user?.role === 'student') {
      fetchEnrollments();
    }
  }, [user]);

  // Update URL when filters change
  useEffect(() => {
    const newParams = new URLSearchParams();
    Object.entries(filters).forEach(([key, value]) => {
      if (value && value !== 'all') {
        newParams.set(key, value);
      }
    });
    setSearchParams(newParams);
  }, [filters, setSearchParams]);

  const fetchCourses = async () => {
    try {
      const data = await courseAPI.getCourses();
      setCourses(data);
    } catch (error) {
      handleApiError(error);
    } finally {
      setLoading(false);
    }
  };

  const fetchEnrollments = async () => {
    try {
      const data = await courseAPI.getMyEnrollments();
      setEnrollments(data);
    } catch (error) {
      handleApiError(error);
    }
  };

  const handleEnrollCourse = async (courseId) => {
    setEnrolling(prev => new Set(prev).add(courseId));
    try {
      await courseAPI.enrollCourse(courseId);
      toast.success('Enrolled successfully!');
      fetchEnrollments();
    } catch (error) {
      handleApiError(error);
    } finally {
      setEnrolling(prev => {
        const newSet = new Set(prev);
        newSet.delete(courseId);
        return newSet;
      });
    }
  };

  const isEnrolled = (courseId) => {
    return enrollments.some(enrollment => enrollment.course_id === courseId);
  };

  const getEnrollmentProgress = (courseId) => {
    const enrollment = enrollments.find(e => e.course_id === courseId);
    return enrollment ? enrollment.progress_percentage : 0;
  };

  // Filter and sort courses
  const filteredAndSortedCourses = useMemo(() => {
    let filtered = courses.filter(course => {
      const matchesSearch = course.title.toLowerCase().includes(filters.search.toLowerCase()) ||
                            course.description.toLowerCase().includes(filters.search.toLowerCase()) ||
                            course.instructor.toLowerCase().includes(filters.search.toLowerCase());
      
      const matchesDifficulty = filters.difficulty === 'all' || course.difficulty_level === filters.difficulty;
      const matchesCategory = filters.category === 'all' || course.category === filters.category;
      const matchesRating = filters.rating === 'all' || course.rating >= parseFloat(filters.rating);
      const matchesDuration = filters.duration === 'all' || 
        (filters.duration === 'short' && course.duration_hours <= 5) ||
        (filters.duration === 'medium' && course.duration_hours > 5 && course.duration_hours <= 20) ||
        (filters.duration === 'long' && course.duration_hours > 20);

      return matchesSearch && matchesDifficulty && matchesCategory && matchesRating && matchesDuration;
    });

    // Sort courses
    filtered.sort((a, b) => {
      switch (filters.sort) {
        case 'newest':
          return new Date(b.created_at) - new Date(a.created_at);
        case 'oldest':
          return new Date(a.created_at) - new Date(b.created_at);
        case 'rating':
          return b.rating - a.rating;
        case 'popularity':
          return b.enrollment_count - a.enrollment_count;
        case 'title':
          return a.title.localeCompare(b.title);
        case 'duration':
          return a.duration_hours - b.duration_hours;
        default:
          return 0;
      }
    });

    return filtered;
  }, [courses, filters]);

  const categories = [...new Set(courses.map(course => course.category).filter(Boolean))];

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-100">
        <div className="text-center">
          <div className="animate-spin rounded-full h-16 w-16 border-b-4 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600 text-lg">Loading courses...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 via-blue-50 to-indigo-50">
      {/* Enhanced Header */}
      <div className="bg-white shadow-sm border-b border-gray-100">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between">
            <div className="flex items-center space-x-4 mb-4 lg:mb-0">
              <div className="bg-gradient-to-r from-blue-600 to-purple-600 p-3 rounded-xl shadow-lg">
                <BookOpen className="h-8 w-8 text-white" />
              </div>
              <div>
                <h1 className="text-3xl font-bold bg-gradient-to-r from-gray-900 to-blue-900 bg-clip-text text-transparent">
                  Course Catalog
                </h1>
                <p className="text-gray-600 mt-1">
                  Discover {courses.length} courses tailored for your growth
                </p>
              </div>
            </div>
            
            <div className="flex items-center space-x-3">
              {user?.role === 'admin' && (
                <button
                  onClick={() => setShowCreateModal(true)}
                  className="flex items-center px-4 py-2 bg-gradient-to-r from-green-600 to-blue-600 text-white rounded-lg hover:from-green-700 hover:to-blue-700 transition-all transform hover:scale-105 shadow-lg"
                >
                  <Plus className="h-4 w-4 mr-2" />
                  Create Course
                </button>
              )}
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Search and Filter Bar */}
        <div className="mb-8">
          <div className="bg-white rounded-xl shadow-lg p-6 border border-gray-100">
            {/* Search Bar */}
            <div className="flex flex-col lg:flex-row gap-4 mb-4">
              <div className="flex-1 relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-5 w-5" />
                <input
                  type="text"
                  placeholder="Search courses, instructors, or topics..."
                  value={filters.search}
                  onChange={(e) => setFilters(prev => ({ ...prev, search: e.target.value }))}
                  className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                />
                {filters.search && (
                  <button
                    onClick={() => setFilters(prev => ({ ...prev, search: '' }))}
                    className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600"
                  >
                    <X className="h-4 w-4" />
                  </button>
                )}
              </div>
              
              <button
                onClick={() => setShowFilters(!showFilters)}
                className={`flex items-center px-4 py-3 border rounded-lg transition-all ${
                  showFilters 
                    ? 'border-blue-500 bg-blue-50 text-blue-700' 
                    : 'border-gray-300 hover:border-gray-400'
                }`}
              >
                <Filter className="h-4 w-4 mr-2" />
                Filters
                <ChevronDown className={`h-4 w-4 ml-2 transition-transform ${showFilters ? 'rotate-180' : ''}`} />
              </button>
            </div>

            {/* Advanced Filters */}
            {showFilters && (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-4 pt-4 border-t border-gray-200">
                <FilterSelect
                  label="Difficulty"
                  value={filters.difficulty}
                  onChange={(value) => setFilters(prev => ({ ...prev, difficulty: value }))}
                  options={[
                    { value: 'all', label: 'All Levels' },
                    { value: 'beginner', label: 'Beginner' },
                    { value: 'intermediate', label: 'Intermediate' },
                    { value: 'advanced', label: 'Advanced' }
                  ]}
                />
                
                <FilterSelect
                  label="Category"
                  value={filters.category}
                  onChange={(value) => setFilters(prev => ({ ...prev, category: value }))}
                  options={[
                    { value: 'all', label: 'All Categories' },
                    ...categories.map(cat => ({ value: cat, label: cat }))
                  ]}
                />
                
                <FilterSelect
                  label="Rating"
                  value={filters.rating}
                  onChange={(value) => setFilters(prev => ({ ...prev, rating: value }))}
                  options={[
                    { value: 'all', label: 'Any Rating' },
                    { value: '4', label: '4+ Stars' },
                    { value: '3', label: '3+ Stars' },
                    { value: '2', label: '2+ Stars' }
                  ]}
                />
                
                <FilterSelect
                  label="Duration"
                  value={filters.duration}
                  onChange={(value) => setFilters(prev => ({ ...prev, duration: value }))}
                  options={[
                    { value: 'all', label: 'Any Duration' },
                    { value: 'short', label: 'Short (≤5h)' },
                    { value: 'medium', label: 'Medium (5-20h)' },
                    { value: 'long', label: 'Long (20h+)' }
                  ]}
                />
                
                <FilterSelect
                  label="Sort By"
                  value={filters.sort}
                  onChange={(value) => setFilters(prev => ({ ...prev, sort: value }))}
                  options={[
                    { value: 'newest', label: 'Newest First' },
                    { value: 'oldest', label: 'Oldest First' },
                    { value: 'rating', label: 'Highest Rated' },
                    { value: 'popularity', label: 'Most Popular' },
                    { value: 'title', label: 'Alphabetical' },
                    { value: 'duration', label: 'Shortest First' }
                  ]}
                />
              </div>
            )}
          </div>
        </div>

        {/* Results Header */}
        <div className="flex items-center justify-between mb-6">
          <div>
            <h2 className="text-xl font-semibold text-gray-900">
              {filteredAndSortedCourses.length} {filteredAndSortedCourses.length === 1 ? 'Course' : 'Courses'} Found
            </h2>
            {Object.values(filters).some(value => value && value !== 'all' && value !== 'newest') && (
              <button
                onClick={() => setFilters({
                  search: '',
                  difficulty: 'all',
                  category: 'all',
                  rating: 'all',
                  duration: 'all',
                  sort: 'newest'
                })}
                className="text-sm text-blue-600 hover:text-blue-700 mt-1"
              >
                Clear all filters
              </button>
            )}
          </div>
          
          <div className="flex items-center space-x-2">
            <button
              onClick={() => setViewMode('grid')}
              className={`p-2 rounded-lg transition-colors ${
                viewMode === 'grid' 
                  ? 'bg-blue-100 text-blue-600' 
                  : 'text-gray-400 hover:text-gray-600'
              }`}
            >
              <Grid className="h-5 w-5" />
            </button>
            <button
              onClick={() => setViewMode('list')}
              className={`p-2 rounded-lg transition-colors ${
                viewMode === 'list' 
                  ? 'bg-blue-100 text-blue-600' 
                  : 'text-gray-400 hover:text-gray-600'
              }`}
            >
              <List className="h-5 w-5" />
            </button>
          </div>
        </div>

        {/* Course Grid/List */}
        {filteredAndSortedCourses.length > 0 ? (
          <div className={viewMode === 'grid' 
            ? 'grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6'
            : 'space-y-4'
          }>
            {filteredAndSortedCourses.map((course) => (
              viewMode === 'grid' ? (
                <CourseCard
                  key={course.id}
                  course={course}
                  user={user}
                  isEnrolled={isEnrolled(course.id)}
                  isEnrolling={enrolling.has(course.id)}
                  progress={getEnrollmentProgress(course.id)}
                  onEnroll={() => handleEnrollCourse(course.id)}
                  onEdit={() => console.log('Edit course:', course.id)}
                  onDelete={() => console.log('Delete course:', course.id)}
                  navigate={navigate}
                />
              ) : (
                <CourseListItem
                  key={course.id}
                  course={course}
                  user={user}
                  isEnrolled={isEnrolled(course.id)}
                  isEnrolling={enrolling.has(course.id)}
                  progress={getEnrollmentProgress(course.id)}
                  onEnroll={() => handleEnrollCourse(course.id)}
                  navigate={navigate}
                />
              )
            ))}
          </div>
        ) : (
          <EmptyState
            icon={BookMarked}
            title="No courses found"
            description={
              Object.values(filters).some(value => value && value !== 'all' && value !== 'newest')
                ? 'Try adjusting your search criteria or filters'
                : 'No courses available yet'
            }
            action={
              <button
                onClick={() => setFilters({
                  search: '',
                  difficulty: 'all',
                  category: 'all',
                  rating: 'all',
                  duration: 'all',
                  sort: 'newest'
                })}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
              >
                Clear Filters
              </button>
            }
          />
        )}
      </div>

      {/* Create Course Modal */}
      {showCreateModal && (
        <CreateCourseModal
          onClose={() => setShowCreateModal(false)}
          onSuccess={() => {
            setShowCreateModal(false);
            fetchCourses();
          }}
        />
      )}
    </div>
  );
};

const FilterSelect = ({ label, value, onChange, options }) => (
  <div>
    <label className="block text-sm font-medium text-gray-700 mb-1">{label}</label>
    <select
      value={value}
      onChange={(e) => onChange(e.target.value)}
      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent text-sm"
    >
      {options.map(option => (
        <option key={option.value} value={option.value}>
          {option.label}
        </option>
      ))}
    </select>
  </div>
);

const CourseCard = ({ course, user, isEnrolled, isEnrolling, progress, onEnroll, onEdit, onDelete, navigate }) => {
  const difficultyColors = {
    beginner: 'bg-green-100 text-green-800',
    intermediate: 'bg-yellow-100 text-yellow-800',
    advanced: 'bg-red-100 text-red-800'
  };

  const handleCardClick = () => {
    if (isEnrolled || user?.role === 'admin') {
      navigate(`/course/${course.id}`);
    }
  };

  return (
    <div className="bg-white rounded-xl shadow-lg overflow-hidden hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1 border border-gray-100">
      {/* Course Image/Thumbnail */}
      <div className="h-48 bg-gradient-to-r from-blue-500 to-purple-600 relative overflow-hidden">
        {course.thumbnail_url ? (
          <img
            src={course.thumbnail_url}
            alt={course.title}
            className="w-full h-full object-cover transition-transform hover:scale-110"
          />
        ) : (
          <div className="w-full h-full flex items-center justify-center">
            <BookOpen className="h-16 w-16 text-white opacity-50" />
          </div>
        )}
        
        {/* Badges */}
        <div className="absolute top-3 left-3 flex flex-col space-y-2">
          {course.is_featured && (
            <span className="bg-gradient-to-r from-yellow-400 to-orange-500 text-white px-2 py-1 rounded-full text-xs font-medium flex items-center">
              <Sparkles className="h-3 w-3 mr-1" />
              Featured
            </span>
          )}
          {course.is_free && (
            <span className="bg-green-500 text-white px-2 py-1 rounded-full text-xs font-medium">
              Free
            </span>
          )}
        </div>

        {/* Admin Actions */}
        {user?.role === 'admin' && (
          <div className="absolute top-3 right-3 flex space-x-2">
            <button
              onClick={(e) => {
                e.stopPropagation();
                onEdit();
              }}
              className="p-2 bg-white bg-opacity-90 rounded-full hover:bg-opacity-100 transition-all shadow-lg"
            >
              <Edit className="h-4 w-4 text-gray-600" />
            </button>
            <button
              onClick={(e) => {
                e.stopPropagation();
                onDelete();
              }}
              className="p-2 bg-white bg-opacity-90 rounded-full hover:bg-opacity-100 transition-all shadow-lg"
            >
              <Trash2 className="h-4 w-4 text-red-600" />
            </button>
          </div>
        )}

        {/* Progress Overlay for Enrolled Students */}
        {isEnrolled && user?.role === 'student' && (
          <div className="absolute bottom-0 left-0 right-0 bg-black bg-opacity-50 text-white p-2">
            <div className="flex justify-between text-xs mb-1">
              <span>Progress</span>
              <span>{Math.round(progress)}%</span>
            </div>
            <div className="w-full bg-gray-300 rounded-full h-1">
              <div
                className="bg-blue-400 h-1 rounded-full transition-all duration-300"
                style={{ width: `${progress}%` }}
              ></div>
            </div>
          </div>
        )}
      </div>

      {/* Course Content */}
      <div className="p-6">
        <div className="flex justify-between items-start mb-3">
          <h3 className="text-lg font-semibold text-gray-900 line-clamp-2 hover:text-blue-600 transition-colors cursor-pointer">
            {course.title}
          </h3>
          <span className={`px-2 py-1 rounded-full text-xs font-medium ${difficultyColors[course.difficulty_level]} flex-shrink-0 ml-2`}>
            {course.difficulty_level}
          </span>
        </div>

        <p className="text-gray-600 text-sm mb-4 line-clamp-3">
          {course.description}
        </p>

        {/* Course Meta */}
        <div className="flex items-center text-sm text-gray-500 mb-4 space-x-4">
          <div className="flex items-center">
            <Clock className="h-4 w-4 mr-1" />
            {course.duration_hours}h
          </div>
          <div className="flex items-center">
            <Users className="h-4 w-4 mr-1" />
            {course.enrollment_count}
          </div>
          <div className="flex items-center">
            <Star className="h-4 w-4 mr-1 text-yellow-500 fill-current" />
            {course.rating?.toFixed(1) || 'N/A'}
          </div>
        </div>

        {/* Instructor */}
        <div className="flex items-center mb-4">
          <div className="w-8 h-8 bg-gray-300 rounded-full flex items-center justify-center mr-2">
            <Users className="h-4 w-4 text-gray-600" />
          </div>
          <span className="text-sm text-gray-700">{course.instructor}</span>
        </div>

        {/* Tags */}
        {course.tags && course.tags.length > 0 && (
          <div className="flex flex-wrap gap-2 mb-4">
            {course.tags.slice(0, 3).map((tag, index) => (
              <span
                key={index}
                className="px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded-full hover:bg-gray-200 transition-colors"
              >
                {tag}
              </span>
            ))}
            {course.tags.length > 3 && (
              <span className="px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded-full">
                +{course.tags.length - 3} more
              </span>
            )}
          </div>
        )}

        {/* Action Button */}
        <div className="flex justify-between items-center">
          {user?.role === 'admin' ? (
            <button
              onClick={handleCardClick}
              className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors flex-1"
            >
              <Edit className="h-4 w-4 mr-2" />
              Manage Course
            </button>
          ) : isEnrolled ? (
            <button
              onClick={handleCardClick}
              className="flex items-center px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors flex-1"
            >
              <Play className="h-4 w-4 mr-2" />
              Continue Learning
            </button>
          ) : (
            <button
              onClick={onEnroll}
              disabled={isEnrolling}
              className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed flex-1"
            >
              {isEnrolling ? (
                <>
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                  Enrolling...
                </>
              ) : (
                <>
                  <Plus className="h-4 w-4 mr-2" />
                  Enroll Now
                </>
              )}
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

const CourseListItem = ({ course, user, isEnrolled, isEnrolling, progress, onEnroll, navigate }) => {
  const difficultyColors = {
    beginner: 'bg-green-100 text-green-800',
    intermediate: 'bg-yellow-100 text-yellow-800',
    advanced: 'bg-red-100 text-red-800'
  };

  return (
    <div className="bg-white rounded-xl shadow-lg p-6 border border-gray-100 hover:shadow-xl transition-all">
      <div className="flex items-start space-x-6">
        {/* Thumbnail */}
        <div className="w-24 h-16 bg-gradient-to-r from-blue-500 to-purple-600 rounded-lg flex-shrink-0 flex items-center justify-center">
          {course.thumbnail_url ? (
            <img
              src={course.thumbnail_url}
              alt={course.title}
              className="w-full h-full object-cover rounded-lg"
            />
          ) : (
            <BookOpen className="h-8 w-8 text-white opacity-75" />
          )}
        </div>

        {/* Content */}
        <div className="flex-1 min-w-0">
          <div className="flex items-start justify-between mb-2">
            <h3 className="text-lg font-semibold text-gray-900 hover:text-blue-600 transition-colors cursor-pointer">
              {course.title}
            </h3>
            <div className="flex items-center space-x-2 ml-4">
              <span className={`px-2 py-1 rounded-full text-xs font-medium ${difficultyColors[course.difficulty_level]}`}>
                {course.difficulty_level}
              </span>
              {course.is_featured && (
                <span className="bg-gradient-to-r from-yellow-400 to-orange-500 text-white px-2 py-1 rounded-full text-xs font-medium flex items-center">
                  <Sparkles className="h-3 w-3 mr-1" />
                  Featured
                </span>
              )}
            </div>
          </div>

          <p className="text-gray-600 text-sm mb-3 line-clamp-2">{course.description}</p>

          <div className="flex items-center text-sm text-gray-500 mb-3 space-x-6">
            <div className="flex items-center">
              <Users className="h-4 w-4 mr-1" />
              {course.instructor}
            </div>
            <div className="flex items-center">
              <Clock className="h-4 w-4 mr-1" />
              {course.duration_hours}h
            </div>
            <div className="flex items-center">
              <Users className="h-4 w-4 mr-1" />
              {course.enrollment_count} students
            </div>
            <div className="flex items-center">
              <Star className="h-4 w-4 mr-1 text-yellow-500 fill-current" />
              {course.rating?.toFixed(1) || 'N/A'}
            </div>
          </div>

          {/* Progress Bar for Enrolled Students */}
          {isEnrolled && user?.role === 'student' && (
            <div className="mb-3">
              <div className="flex justify-between text-sm text-gray-600 mb-1">
                <span>Progress</span>
                <span>{Math.round(progress)}%</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2">
                <div
                  className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                  style={{ width: `${progress}%` }}
                ></div>
              </div>
            </div>
          )}

          <div className="flex items-center justify-between">
            <div className="flex flex-wrap gap-2">
              {course.tags?.slice(0, 3).map((tag, index) => (
                <span
                  key={index}
                  className="px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded"
                >
                  {tag}
                </span>
              ))}
            </div>

            {/* Action Button */}
            {user?.role === 'admin' ? (
              <button
                onClick={() => navigate(`/course/${course.id}`)}
                className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
              >
                <Edit className="h-4 w-4 mr-2" />
                Manage
              </button>
            ) : isEnrolled ? (
              <button
                onClick={() => navigate(`/course/${course.id}`)}
                className="flex items-center px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors"
              >
                <Play className="h-4 w-4 mr-2" />
                Continue
              </button>
            ) : (
              <button
                onClick={onEnroll}
                disabled={isEnrolling}
                className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50"
              >
                {isEnrolling ? (
                  <>
                    <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                    Enrolling...
                  </>
                ) : (
                  <>
                    <Plus className="h-4 w-4 mr-2" />
                    Enroll
                  </>
                )}
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

const CreateCourseModal = ({ onClose, onSuccess }) => {
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    instructor: '',
    duration_hours: '',
    difficulty_level: 'beginner',
    category: '',
    tags: ''
  });
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);

    try {
      const courseData = {
        ...formData,
        duration_hours: parseFloat(formData.duration_hours),
        tags: formData.tags.split(',').map(tag => tag.trim()).filter(Boolean)
      };

      await courseAPI.createCourse(courseData);
      toast.success('Course created successfully!');
      onSuccess();
    } catch (error) {
      handleApiError(error);
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto shadow-2xl">
        <div className="p-6">
          <div className="flex justify-between items-center mb-6">
            <div>
              <h2 className="text-2xl font-bold text-gray-900">Create New Course</h2>
              <p className="text-gray-600 mt-1">Add a new course to the platform</p>
            </div>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 transition-colors p-2"
            >
              <X className="h-6 w-6" />
            </button>
          </div>

          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Course Title *
                </label>
                <input
                  type="text"
                  name="title"
                  value={formData.title}
                  onChange={handleChange}
                  required
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                  placeholder="Enter an engaging course title"
                />
              </div>

              <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Description *
                </label>
                <textarea
                  name="description"
                  value={formData.description}
                  onChange={handleChange}
                  required
                  rows={4}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                  placeholder="Describe what students will learn in this course"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Instructor *
                </label>
                <input
                  type="text"
                  name="instructor"
                  value={formData.instructor}
                  onChange={handleChange}
                  required
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                  placeholder="Instructor name"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Category
                </label>
                <input
                  type="text"
                  name="category"
                  value={formData.category}
                  onChange={handleChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                  placeholder="e.g., Programming, Design, Business"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Duration (hours) *
                </label>
                <input
                  type="number"
                  name="duration_hours"
                  value={formData.duration_hours}
                  onChange={handleChange}
                  required
                  min="0.5"
                  step="0.5"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                  placeholder="e.g., 10.5"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Difficulty Level *
                </label>
                <select
                  name="difficulty_level"
                  value={formData.difficulty_level}
                  onChange={handleChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                >
                  <option value="beginner">Beginner</option>
                  <option value="intermediate">Intermediate</option>
                  <option value="advanced">Advanced</option>
                </select>
              </div>

              <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Tags
                </label>
                <input
                  type="text"
                  name="tags"
                  value={formData.tags}
                  onChange={handleChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                  placeholder="Enter tags separated by commas (e.g., JavaScript, React, Frontend)"
                />
                <p className="text-sm text-gray-500 mt-1">
                  Separate multiple tags with commas to help students find your course
                </p>
              </div>
            </div>

            <div className="flex justify-end space-x-4 pt-6 border-t border-gray-200">
              <button
                type="button"
                onClick={onClose}
                className="px-6 py-3 text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors font-medium"
              >
                Cancel
              </button>
              <button
                type="submit"
                disabled={loading}
                className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed font-medium flex items-center"
              >
                {loading ? (
                  <>
                    <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                    Creating...
                  </>
                ) : (
                  <>
                    <Plus className="h-4 w-4 mr-2" />
                    Create Course
                  </>
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

const EmptyState = ({ icon: Icon, title, description, action }) => (
  <div className="text-center py-16">
    <Icon className="h-16 w-16 text-gray-400 mx-auto mb-4" />
    <h3 className="text-xl font-medium text-gray-900 mb-2">{title}</h3>
    <p className="text-gray-500 mb-6 max-w-md mx-auto">{description}</p>
    {action && action}
  </div>
);

export default CourseView;
